import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { MarkerWithLabel } from "react-google-maps/lib/components/addons/MarkerWithLabel";
import verde_img from './../resources/img/verde.png';
import rojo_img from './../resources/img/rojo.png';
import gris_img from './../resources/img/gris.png';

class SpiderMarker extends Component {
    render() {
        const { title, name, position, onMarkerClick } = this.props;
        const pointmap = new window.google.maps.Point(0, 0);
        return (
            <div>
                {/* <Marker
                    title={title}
                    name={name}
                    position={position}
                    onClick={onMarkerClick}
                    icon={verde_img}
                /> */}
                <MarkerWithLabel
                    position={this.props.posicion}
                    labelAnchor={pointmap}
                    labelClass="label_mapa"
                    icon={verde_img}
                    onClick={this.props.onMarkerClick}
                >
                    <div>{this.props.nombre}</div>
                </MarkerWithLabel>
            </div>
        );
    }
}

SpiderMarker.propTypes = {
    estado: PropTypes.string,
    onMarkerClick: PropTypes.func,
};

export default SpiderMarker;