import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Avatar from '@material-ui/core/Avatar';
import Popper from '@material-ui/core/Popper';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import SpiderLogo from '../resources/img/logospider.png';
import ImgEjemplo from '../resources/img/logoejemp.png';

class HeaderContainer extends Component {

    state = {
        open: false,
        anchorEl: null,
    }

    handleToggle = event => {
        const { currentTarget } = event;
        this.setState(state => ({ 
            anchorEl: currentTarget,
            open: !state.open 
        }));
      };

    handleClose = event => {
        this.setState({ open: false });
    };

    handleLogout = () => {
        this.setState({ open: false });
        // localStorage.setItem('user', 'NA');
        // const linkto = document.createElement("a");
        // const direccion = "#/";
        // linkto.href = direccion;
        // linkto.click(); 
    }


    render() {
        
        const { anchorEl,open } = this.state;
        return (
            <AppBar position="fixed" color="inherit" >
                    <Toolbar>
                        <img src={SpiderLogo} alt="Logo Spider" className="logo-superior"></img>
                        <div className="header-grow"></div>
                        <div>
                            <Avatar alt="Logo Cliente" src={ImgEjemplo} onClick={this.handleToggle} className="logo-cliente"></Avatar>
                            <Popper open={open} placement="bottom" anchorEl={anchorEl} transition className="menu-popper">
                                {({ TransitionProps, placement }) => (
                                    <Grow
                                        {...TransitionProps}
                                        id="menu-list-grow"
                                        style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                                        >
                                        <Paper>
                                        <ClickAwayListener onClickAway={this.handleClose}>
                                            <MenuList>
                                                <MenuItem onClick={this.handleClose}>
                                                    <ListItemIcon>
                                                        <FontAwesomeIcon icon="user"/>
                                                    </ListItemIcon>
                                                    <ListItemText inset primary="Perfil"></ListItemText>
                                                </MenuItem>
                                                <MenuItem onClick={this.handleLogout}>   
                                                    <ListItemIcon>
                                                        <FontAwesomeIcon icon="sign-out-alt"/>
                                                    </ListItemIcon>
                                                    <ListItemText inset primary="Salir"></ListItemText>
                                                </MenuItem>
                                            </MenuList>
                                        </ClickAwayListener>
                                        </Paper>
                                    </Grow>
                                )}
                            </Popper>
                        </div>
                    </Toolbar>
            </AppBar>
        );
    }
}

export default HeaderContainer;