import React, { Component } from 'react';

class SpiderValidator extends Component {
    render() {
        const {activo} = this.props;
        let currentClass = "spider_validator";
        if (!activo) {
            currentClass = "spider_validator esconder";
        }
        return (
            <div className={currentClass}>
                Seleccione por favor
            </div>
        );
    }
}

export default SpiderValidator;