import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import NoSsr from '@material-ui/core/NoSsr';
import { DialogActions } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import moment from 'moment';
import 'moment/locale/es';
import MomentUtils from '@date-io/moment';
import Select from 'react-select';
import { MuiPickersUtilsProvider, DatePicker, DateTimePicker } from 'material-ui-pickers';
import { fetchConsultaVehiculo, fetchConsultaFechaInit, fetchConsultaFechaEnd } from './../actions';
import SpiderValidator from '../components/SpiderValidator';
import './styles.css';

class ModalContainer extends Component {

    constructor() {
        super();
        this.state = {
            FechaInicial: moment().set({hour:0,minute:0,second:1}),
            FechaFinal: moment().set({hour:23,minute:59,second:59}),
        }
    }

    componentDidMount() {
        this.props.getConsultaFechaInit(this.state.FechaInicial);
        this.props.getConsultaFechaEnd(this.state.FechaFinal);
    }

    handleVehiculoChange = event => {
        //console.log(event.value);
        this.props.getConsultaVehiculo([event.value,event.label]);
    }

    handleFechaInicialChange = date => {
        //console.log(date);
        this.setState({ 
            FechaInicial: date
        });
        this.props.getConsultaFechaInit(date);
    };

    handleFechaFinalChange = date => {
        //console.log(date);
        this.setState({ 
            FechaFinal: date
        });
        this.props.getConsultaFechaEnd(date);
    };

    render() {

        const { posiciones, boolItinerarios, boolCalor, boolGeocercas, 
                boolValidacion, onCloseModal, onGetItinerarios, onGetMapaCalor } = this.props;
        const { FechaInicial, FechaFinal } = this.state;
        const vehiculos = [];

        posiciones.map( posicion => (
            vehiculos.push({ value: posicion.serial, label: posicion.ubiSerial })
        ));

        return (
            <div>
                <Dialog
                    PaperProps={{className:"dialog_overflow"}}
                    open={boolItinerarios}
                    onClose={onCloseModal}
                    fullWidth={true}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogTitle>
                        Calcular Itinerarios
                    </DialogTitle>
                    <div className="dialog_content_overflow">
                        <Grid container>
                            <Grid item xs={6}>
                                <div className="dialog_content_text">
                                    Elegir vehículo de interés:
                                </div>
                                <form className="form_modal" noValidate>
                                    <FormControl className="formControl_modal">
                                        <NoSsr>
                                            <Select
                                                    defaultValue="vehiculo1"
                                                    options={vehiculos}
                                                    placeholder="Vehiculo.."
                                                    isSearchable
                                                    menuPosition='absolute'
                                                    menuPlacement='bottom'
                                                    onChange={this.handleVehiculoChange}
                                            />
                                        </NoSsr>
                                    </FormControl>
                                    <SpiderValidator activo={boolValidacion} />
                                </form>
                            </Grid>
                            <Grid item xs={6}>
                                <MuiPickersUtilsProvider utils={MomentUtils}>
                                    <div className="picker_modal">
                                        <DateTimePicker
                                            keyboard
                                            ampm={false}
                                            label="Fecha Inicial"
                                            onError={console.log}
                                            value={FechaInicial}
                                            onChange={this.handleFechaInicialChange}
                                            format='DD/MM/YYYY hh:mm A'
                                            disableOpenOnEnter
                                        />
                                    </div>
                                    <div className="picker_modal">
                                        <DateTimePicker
                                            keyboard
                                            ampm={false}
                                            label="Fecha Inicial"
                                            onError={console.log}
                                            value={FechaFinal}
                                            onChange={this.handleFechaFinalChange}
                                            format='DD/MM/YYYY hh:mm A'
                                            disableOpenOnEnter
                                        />
                                    </div>
                                </MuiPickersUtilsProvider>
                            </Grid>
                        </Grid>
                    </div>
                    <DialogActions>
                        <Button onClick={onCloseModal} color="primary">
                            Cancelar
                        </Button>
                        <Button onClick={onGetItinerarios} color="primary">
                            Enviar
                        </Button>
                    </DialogActions>
                </Dialog>
                <Dialog
                    PaperProps={{className:"dialog_overflow"}}
                    open={boolCalor}
                    onClose={onCloseModal}
                    fullWidth={true}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogTitle>
                        Mapa de Calor
                    </DialogTitle>
                    <div className="dialog_content_overflow">
                        <Grid container>
                            <Grid item xs={6}>
                                <div className="dialog_content_text">
                                    Elegir vehículo de interés:
                                </div>
                                <form className="form_modal" noValidate>
                                    <FormControl className="formControl_modal">
                                        <NoSsr>
                                            <Select
                                            className="select_modal"
                                            defaultValue="vehiculo1"
                                            options={vehiculos}
                                            placeholder="Vehiculo.."
                                            isSearchable
                                            menuPosition='absolute'
                                            menuPlacement='bottom'
                                            onChange={this.handleVehiculoChange}
                                            />
                                        </NoSsr>
                                    </FormControl>
                                    <SpiderValidator activo={boolValidacion} />
                                </form>
                            </Grid>
                            <Grid item xs={6}>
                                <MuiPickersUtilsProvider utils={MomentUtils}>
                                    <div className="picker_modal">
                                        <DateTimePicker
                                            keyboard
                                            ampm={false}
                                            label="Fecha Inicial"
                                            onError={console.log}
                                            value={FechaInicial}
                                            onChange={this.handleFechaInicialChange}
                                            format='DD/MM/YYYY hh:mm A'
                                            disableOpenOnEnter
                                        />
                                    </div>
                                    <div className="picker_modal">
                                        <DateTimePicker
                                            keyboard
                                            ampm={false}
                                            label="Fecha Inicial"
                                            onError={console.log}
                                            value={FechaFinal}
                                            onChange={this.handleFechaFinalChange}
                                            format='DD/MM/YYYY hh:mm A'
                                            disableOpenOnEnter
                                        />
                                    </div>
                                </MuiPickersUtilsProvider>
                            </Grid>
                        </Grid>
                    </div>
                    <DialogActions>
                        <Button onClick={onCloseModal} color="primary">
                            Cancelar
                        </Button>
                        <Button onClick={onGetMapaCalor} color="primary">
                            Enviar
                        </Button>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={boolGeocercas}
                    onClose={onCloseModal}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogTitle>
                        Geocercas
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Dialogo para elegir Geocercas
                        </DialogContentText>
                    </DialogContent>
                </Dialog>
            </div>
        );
    }
}

ModalContainer.propTypes = {
    boolItinerarios: PropTypes.bool.isRequired,
    boolGeocercas: PropTypes.bool.isRequired,
    boolCalor: PropTypes.bool.isRequired,
    onCloseModal: PropTypes.func,
    onGetItinerarios: PropTypes.func,
};

const mapDispatchToProps = dispatch => ({
    getConsultaVehiculo: value => dispatch(fetchConsultaVehiculo(value)),
    getConsultaFechaInit: value => dispatch(fetchConsultaFechaInit(value)),
    getConsultaFechaEnd: value => dispatch(fetchConsultaFechaEnd(value)),
});

const mapStatetoProps = state => ({
    consulta_vehiculo: state.consulta_vehiculo,
    consulta_fechainit: state.consulta_fechainit,
    consulta_fechaend: state.consulta_fechaend,
});

export default connect(mapStatetoProps, mapDispatchToProps)(ModalContainer);