import React, { Component } from 'react';
import moment from 'moment';
import { withScriptjs, withGoogleMap, GoogleMap, Polyline, Marker } from 'react-google-maps';
import { MarkerClusterer } from 'react-google-maps/lib/components/addons/MarkerClusterer';
import { HeatmapLayer } from 'react-google-maps/lib/components/visualization/HeatmapLayer';
import { DrawingManager } from 'react-google-maps/lib/components/drawing/DrawingManager';
import SpiderMarker from './SpiderMarker';
import inicio from './../resources/img/inicio.png';
import fin from './../resources/img/fin.png';
import velocidad from './../resources/img/velocidad.png';
import frenos from './../resources/img/frenos.png';
import aceleracion from './../resources/img/aceleracion.png';
import brusco from './../resources/img/brusco.png';

const SpiderMap = withScriptjs(withGoogleMap((props) => {
    return (
        <GoogleMap
            zoom={props.zoom}
            center={ props.center }
            ref={props.mapita}
            defaultOptions={
              {
                  mapTypeControl: false
              }
            }
          >
          <MarkerClusterer
                averageCenter
                enableRetinaIcons >
                 {
                    props.posiciones.map( posicion => {
                        let estado = 'verde_img';
                        if ( moment(posicion.location._Date).isBefore(moment().add(6, 'hours')) ) {
                            estado = 'verde_img';
                        }
                        return(
                            <SpiderMarker 
                                key = {posicion.serial}
                                nombre = {posicion.ubiSerial}
                                estado = {estado}
                                posicion ={{
                                    lat: posicion.location.location.coordinates[0],
                                    lng: posicion.location.location.coordinates[1]
                                    }}
                                onMarkerClick={props.onMarkerClick}
                            />
                        )
                    })
                }
            </MarkerClusterer>
             <Polyline path={props.viaje}></Polyline>
              {
                props.marcadores.map( marcador => {
                //console.log(marcador);
                let imagen = inicio;
                if (marcador.event === "Fin")
                  {imagen = fin;}
                if (marcador.event === "Speeding API")
                  {imagen = velocidad;}
                if (marcador.event === "Quick Lane Change")
                  {imagen = brusco;}  
                if (marcador.event === "Hard Deceleration")
                  {imagen = frenos;} 
                if (marcador.event === "Hard Acceleration")
                  {imagen = aceleracion;}
                    return(
                        <Marker 
                            key = {marcador.id}
                            position={{
                                lat: marcador.latitude,
                                lng: marcador.longitude
                                }}
                            icon = {imagen}
                        />
                    )
                })
            }
            <HeatmapLayer 
                data= {props.calor}
            />
            <DrawingManager
              options={{
                drawingControl: props.geocercas,
                drawingControlOptions: {
                  position: window.google.maps.ControlPosition.TOP_CENTER,
                  drawingModes: [
                    window.google.maps.drawing.OverlayType.CIRCLE,
                    window.google.maps.drawing.OverlayType.POLYGON,
                    window.google.maps.drawing.OverlayType.RECTANGLE,
                    window.google.maps.drawing.OverlayType.POLYLINE,
                  ],
                },
              }}
            />
        </GoogleMap>
      );
    }
  ))

export default SpiderMap;