import React, { Component } from 'react';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class SpiderCardViaje extends Component {
    render() {
        const { numero, fecha, hora, tiempo, distancia } = this.props;
        return (
            <Card className="card-viaje">
                <CardActionArea>
                    <div className="card-viaje-content">
                        <div>
                            <div className="card-avatar"><FontAwesomeIcon icon='calendar-alt'/></div>
                            <p className="card-text">{ fecha }</p>
                        </div>
                        <div>
                            <div className="card-avatar"><FontAwesomeIcon icon='clock'/></div>
                            <p className="card-text">{ hora }</p>
                        </div>
                        <div>
                            <div className="card-avatar"><FontAwesomeIcon icon='hourglass-end'/></div>
                            <p className="card-text">{tiempo}</p>
                        </div>
                        <div>
                            <div className="card-avatar"><FontAwesomeIcon icon='arrow-right'/></div>
                            <p className="card-text">{ distancia }</p>
                        </div>
                    </div>
                </CardActionArea>
            </Card>
        );
    }
}

export default SpiderCardViaje;