import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Slide from '@material-ui/core/Slide';
import SpiderWidget from './../components/SpiderWidget';
import './styles.css';

class IndicadoresContainer extends Component {

    constructor() {
        super();
        this.state = {
            colapso : false
        }
    }

    colapso_cambio = () => {
        this.setState({
            colapso : !this.state.colapso
        });
    }

    render() {

        let pupil_class = ["eye pupil"];
        let lid_class = ["eye lid"];
         if (this.state.colapso){
            pupil_class = ["eye pupil pupil--open"];
            lid_class = ["eye lid lid--open"];
         }
         else {
            pupil_class = ["eye pupil pupil--close"];
            lid_class = ["eye lid lid--close"];
         }
        const { colapso } = this.state;

        return (
            <div>
                <Grid container alignItems="center" alignContent="center">
                    <Grid item xs={1}>
                        <div className="eye-container" onClick={this.colapso_cambio}>
                            <svg viewBox='0 0 193.5 116' className="eye-svg">
                                <circle className={pupil_class} cx='96.8' cy='58' r='24'/>
                                <path className={lid_class} d='M5,58L5,58C23.4,26.3,57.6,5,96.8,5c39.3,0,73.8,21.3,91.8,53l0,0c0,0-26.7,53-91.8,53S5,58,5,58z'/>
                            </svg>
                        </div>
                    </Grid>
                    <Grid item xs={11}>
                        <div className="layout-indicadores">
                            <Grid container spacing={24}>
                                <Grid item xs={3}>
                                    <Slide in={colapso} direction="right">
                                        <SpiderWidget color="widget-standar" texto="KM RECORRIDOS" icono="road" valor="72,350"/>
                                    </Slide>
                                </Grid>
                                <Grid item xs={3}>
                                    <Slide in={colapso} direction="right">
                                        <SpiderWidget color="widget-standar" texto="LITROS DE COMBUSTIBLE" icono="gas-pump" valor="49"/>
                                    </Slide>
                                </Grid>
                                <Grid item xs={3}>
                                    <Slide in={colapso} direction="right">
                                        <SpiderWidget color="widget-standar" texto="FALLAS DETECTADAS" icono="exclamation-circle" valor="3"/>
                                    </Slide>
                                </Grid>
                                <Grid item xs={3}>
                                    <Slide in={colapso} direction="right">
                                        <SpiderWidget color="widget-standar" texto="CALIFICACION DE MANEJO" icono="star" valor="95.3"/>
                                    </Slide>
                                </Grid>
                            </Grid>
                        </div>
                    </Grid>
                </Grid>
            </div>
        );
    }
}

export default IndicadoresContainer;