import React, { Component } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import Grid from '@material-ui/core/Grid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
//import { Bar, Radar } from 'react-chartjs-2';

class SpiderCarDetalle extends Component {

    constructor() {
        super();
        this.state = {
            expanded: false
        }
    }

    handleExpandClick = () => {
        this.setState(state => ({ expanded: !state.expanded }));
      };

    render() {

        const {onCloseCardDetalle, onVerUltimoViaje, onVerPoliza} = this.props;

        const inlineStyle_flex = {
            display: 'flex',
          };

        const inlineStyle_expand = {
            transform: 'rotate(0deg)',
            marginLeft: 'auto',
          };

        const inlineStyle_avatar = {
            backgroundColor: '#ffd700',
            width: 80,
            height: 80,
            textAlign: 'center',
            fontsize: 20,
        }

        const bar = {
            labels: ['02/03', '03/03', '04/03', '05/03', '06/03'],
            datasets: [
              {
                label: 'Ultimas Calificaciones Diarias',
                backgroundColor: 'rgba(255,99,132,0.2)',
                borderColor: 'rgba(255,99,132,1)',
                borderWidth: 1,
                hoverBackgroundColor: 'rgba(255,99,132,0.4)',
                hoverBorderColor: 'rgba(255,99,132,1)',
                data: [84, 92, 89, 86, 88],
              },
            ],
          };

          const radar = {
            labels: ['Velocidad', 'Aceleracion', 'Frenado', 'Manejo'],
            datasets: [
              {
                label: 'Conductas de Manejo',
                backgroundColor: 'rgba(179,181,198,0.2)',
                borderColor: 'rgba(179,181,198,1)',
                pointBackgroundColor: 'rgba(179,181,198,1)',
                pointBorderColor: '#fff',
                pointHoverBackgroundColor: '#fff',
                pointHoverBorderColor: 'rgba(179,181,198,1)',
                data: [2, 5, 4, 3],
              },
            ],
          };

          const options = {
            tooltips: {
              enabled: false,
            },
             maintainAspectRatio: false
          }

        return (
            <Card>
                <CardHeader
                    avatar = { <div className="card-avatar-detalle"><FontAwesomeIcon icon='car'/></div> }
                    action = { <IconButton onClick={onCloseCardDetalle}><FontAwesomeIcon icon='times-circle'/></IconButton>}
                    title = "UBI 018007"
                    subheader ="TZP 2586"
                >
                </CardHeader>
                <CardContent>
                    <Grid container>
                        <Grid item xs={8}>
                            <div>
                                <div className="card-avatar"><FontAwesomeIcon icon='user'/></div>
                                <p className="card-text">Jesus Munguia</p>
                            </div>   
                            <div>
                                <div className="card-avatar"><FontAwesomeIcon icon='envelope'/></div>
                                <p className="card-text">jmunguiadeg@gmail.com</p>
                            </div>    
                            <div>
                                <div className="card-avatar"><FontAwesomeIcon icon='phone'/></div>
                                <p className="card-text">2221197773</p>
                            </div> 
                            <div>
                                <div className="card-avatar"><FontAwesomeIcon icon='file-contract'/></div>
                                <p className="card-text">P-055556-55444B</p>
                            </div>
                            <div>
                                <div className="card-avatar"><FontAwesomeIcon icon='hourglass-end'/></div>
                                <p className="card-text">28 H 31 min</p>
                            </div>
                            <div>
                                <div className="card-avatar"><FontAwesomeIcon icon='arrow-right'/></div>
                                <p className="card-text">1650 Km</p>
                            </div>
                        </Grid>
                        <Grid item xs={4}>
                            <div className="card-detalle-calif">
                                <Avatar style={inlineStyle_avatar}>88</Avatar>
                            </div>
                        </Grid>
                    </Grid>
                </CardContent>
                <CardActions disableActionSpacing style={inlineStyle_flex}>
                    <Button size="small" color="primary" onClick={onVerUltimoViaje}>Ultimo Viaje</Button>
                    <Button size="small" color="primary" onClick={onVerPoliza}>Descargar Póliza</Button>
                    <IconButton style={inlineStyle_expand} onClick={this.handleExpandClick} aria-expanded={this.state.expanded} aria-label="Ver mas">
                        <ExpandMoreIcon />
                    </IconButton>
                </CardActions>
                <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
                    <CardContent>
                        {/* <div>
                            <Bar data={bar} options={options}/>
                        </div>
                        <div>
                            <Radar data={radar}/>
                        </div> */}
                        
                        {/* <Radar data={radar} options={options}/> */}
                    </CardContent>
                </Collapse>
            </Card>
        );
    }
}

export default SpiderCarDetalle;