import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Fab from '@material-ui/core/Fab';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './styles.css';

class SpiderButtonToolbox extends Component {

    constructor() {
        super();
        this.state = {
            toogleToolbox : false
        }
    }

    toogle = () => {
        this.setState({
            toogleToolbox : !this.state.toogleToolbox
        });
    }

    render() {
        const { icono, mensaje, color, onSpiderButtonToolboxClick} = this.props;
        return (
            <div className="boton_der">
                <span className="tooltip_toolbox">{mensaje}</span>
                <Fab size="small" color="secondary" onClick={onSpiderButtonToolboxClick}>
                     <FontAwesomeIcon icon={icono}/>
                </Fab>
            </div>
        );
    }
}

SpiderButtonToolbox.propTypes = {
    icono: PropTypes.string.isRequired,
    mensaje: PropTypes.string.isRequired,
    color: PropTypes.string.isRequired,
    onSpiderButtonToolboxClick: PropTypes.func,
};

export default SpiderButtonToolbox;