import { SET_POSICIONES, GET_ITINERARIOS, GET_VIAJE, GET_MAPACALOR,
  SET_CONSULTA_VEHICULO, SET_CONSULTA_FECHAINIT, SET_CONSULTA_FECHAEND, 
  CLEAR_MAP, SET_DATOS_VEHICULO, SET_CURRENT_FIT, SET_GEOCERCAS } from '../actions';

export const estados = (state = {}, action) => {
    switch (action.type) {
      case SET_POSICIONES:
        return {...state, posiciones: action.payload};
      case GET_ITINERARIOS:
        return {...state, itinerarios: action.payload};
      case GET_VIAJE:
        return {...state, viaje: action.payload};
      case GET_MAPACALOR:
        return {...state, calor: action.payload};
      case SET_CONSULTA_VEHICULO:
        return {...state, consulta_vehiculo: action.payload};
      case SET_CONSULTA_FECHAINIT:
        return {...state, consulta_fechainit: action.payload};
      case SET_CONSULTA_FECHAEND:
        return {...state, consulta_fechaend: action.payload};
      case CLEAR_MAP:
        return {...state, itinerarios: [], viaje:{pathviaje: [], eventosviaje: []}, calor:[] }
      case SET_DATOS_VEHICULO:
        return {...state, datos_vehiculos: action.payload }
      case SET_CURRENT_FIT:
        return {...state, current_fit: action.payload }
      case SET_GEOCERCAS:
        return {...state, geocercas: action.payload}
      default:
        return state;
    }
  }