import React, { Component } from 'react';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import './styles.css';

class SpiderCardHeader extends Component {
    render() {
        const {n_itinerarios, vehiculo, fechainit, fechaend} = this.props;
        return (
            <Card className="card-viaje-header">
                <div className="card-viaje-content">
                    <Typography variant="h6">
                        UBI {vehiculo}
                    </Typography>
                    <Typography variant="h6">
                        {n_itinerarios} Viajes
                    </Typography>
                    <p className="card-viaje-fechas">{fechainit}</p>
                    <p className="card-viaje-fechas">{fechaend}</p>
                </div>
            </Card>
        );
    }
}

export default SpiderCardHeader;