import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/Card';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './styles.css';

class SpiderWidget extends Component {

    render() {
        const { color, texto, icono, valor } = this.props;
        const clasewidget = `card-widget ${color}`;

        return (
            <Card>
                <CardContent>
                    <div className={clasewidget}>
                        <div className="icon-widget">
                            <FontAwesomeIcon icon={icono}/>
                        </div>
                        <div className="number-widget">
                           { valor }
                        </div>
                        <div className="text-widget">
                           { texto }
                        </div>
                    </div>
                </CardContent>
            </Card>
        );
    }
}

SpiderWidget.propTypes = {
    color: PropTypes.string.isRequired,
    texto: PropTypes.string.isRequired,
    icono: PropTypes.string.isRequired,
    valor: PropTypes.string.isRequired,
};

export default SpiderWidget;