import React, { Component } from 'react';
import moment from 'moment';
import 'moment/locale/es';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import SpiderCardHeader from './../components/SpiderCardHeader';
import SpiderCardViaje from './../components/SpiderCardViaje';

class ItinerariosContainer extends Component {

    onClickViaje = (device, inicio, fin) => {
        this.props.getViaje({"Device":device, "Inicio":moment(inicio), "Fin":moment(fin)}); 
    }

    render() {
        const {n_itinerarios, itinerarios} = this.props;
        return (
            <div>
                <List className="itinerarios-lista">
                    <ListSubheader>
                        <SpiderCardHeader 
                            n_itinerarios={n_itinerarios} 
                            vehiculo={this.props.consulta_vehiculo[1]} 
                            fechainit={moment(this.props.consulta_fechainit).format('DD-MM-YYYY, h:mm a')}
                            fechaend={moment(this.props.consulta_fechaend).format('DD-MM-YYYY, h:mm a')}
                            />
                    </ListSubheader>
                    {itinerarios.map( itinerario => {
                        return (
                            <ListItem key={itinerario.n} onClick={() => this.onClickViaje(this.props.consulta_vehiculo[0],itinerario.startDate,itinerario.endDate)}>
                                <SpiderCardViaje
                                    numero={itinerario.n}
                                    fecha={moment(itinerario.startDate).format('LL')}
                                    hora={moment(itinerario.startDate).format('hh:mm a')}
                                    tiempo={itinerario.duration}
                                    distancia={itinerario.distance}
                                />
                            </ListItem>
                        )
                    })}
                </List>
            </div>
        );
    }
}

export default ItinerariosContainer;