import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './styles.css';

class SpiderButtonLeft extends Component {
    render() {
        const { icono, mensaje, onSpiderButtonLeftClick} = this.props;
        return (
            <div className="boton_izq" onClick={onSpiderButtonLeftClick}>
                <FontAwesomeIcon icon={icono} className="fa-fw"/>
                <span>{mensaje}</span>
            </div>
        );
    }
}

export default SpiderButtonLeft;