import React, { Component } from 'react';
import { connect } from 'react-redux';
import NoSsr from '@material-ui/core/NoSsr';
import Grid from '@material-ui/core/Grid';
import Select from 'react-select';
import { fetchPosiciones, fetchItinerarios, fetchCalor, fetchClearMap, fetchDatosVehiculo, fetchGeocercas } from './../actions';
import HeaderContainer from './HeaderContainer';
import IndicadoresContainer from './IndicadoresContainer';
import ItinerariosContainer from './ItinerariosContainer';
import SpiderMapContainer from './SpiderMapContainer';
import ModalContainer from './ModalContainer';
import SpiderButtonLeft from '../components/SpiderButtonLeft';
import SpiderButtonToolbox from '../components/SpiderButtonToolbox';
import SpiderCarDetalle from '../components/SpiderCarDetalle';
import './styles.css';

const suggestions = [
    { value: ',5c61e8d760bc2703a75e373c', label: 'Todos' },
    { value: ',5c61e8d760bc2703a75e373c,friendly', label: 'Friendly' },
    { value: ',5c61e8d760bc2703a75e373c,alden', label: 'Alden' },
    { value: ',5c61e8d760bc2703a75e373c,hdi', label: 'HDI' },
    { value: ',5c61e8d760bc2703a75e373c,hgip', label: 'HG/IP' }
  ];

  const renderMapa = () => {
    return <h3>Calculando Mapa...</h3>;
}

class DashboardContainer extends Component {

    constructor() {
        super();

        this.state = {
            modal_itinerarios: false,
            modal_calor: false,
            modal_geocercas: false,
            car_detalle: false,
            modal_validacion: false,
            bool_viaje: false,
            bool_calor: false,
        }
    }

    componentDidMount() {
        this.props.setPosiciones(); 
    }

    onHomeClick = () => {
        //console.log('home');
        const homeLink = document.createElement("a");
        homeLink.href = "#/dashboard";
        homeLink.click();
    }

    onEstadisticasClick = () => {
        console.log('estadisticas');
        const estadisticasLink = document.createElement("a");
        estadisticasLink.href = "#/estadisticas";
        estadisticasLink.click();
    }

    onReportesClick = () => {
        console.log('reportes');
        fetch('http://ubimx.com/api/estadisticas/reporte/general', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'authorization': 'Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiJKZXN1cyBQcnVlYmEiLCJ0ZWxlcGhvbmUiOiIyMjIxMTk3NzczIiwiY29ycmVvIjoiam11bmd1aWFAbWFpbC5jb20iLCJ1c2VybmFtZSI6Ikplc3VzIFBydWViYSIsImp0aSI6ImU1NzZhN2MwLTRlNzItNDE5NS1iZmJlLWI4NGFkNTExZGI4NSIsImV4cCI6MTU2OTk1NDY2MCwiaXNzIjoiaXNzdWVyVGVzdCIsImF1ZCI6ImJlYXJlclRlc3QifQ.W3FsdyjS_yFRZKCTdOs2j6tHlMNbh9i2XUPS2kRpHXs'
              }
        }).then(
            data => (data.json())
        ).then(
            resultado => {
                let pdf = resultado.pdf;
                const downloadLink = document.createElement("a");
                const fileName = `ReporteEjemplo.pdf`;
                downloadLink.href = `data:application/pdf;base64,${pdf}`;
                downloadLink.download = fileName;
                downloadLink.click();
            }
        )
    }

    onConfiguracionClick = () => {
        console.log('configuracion');
    }

    onSelectedSpiderButtonToolbox = toolbox => {
        switch(toolbox) {
            case 'Itinerarios': 
                return this.setState({modal_itinerarios: true});
            case 'Calor': 
                return this.setState({modal_calor: true});
            case 'Geocercas': 
                return this.props.setGeocercas(!this.props.geocercas);
                //this.setState({modal_geocercas: true});
            case 'Limpiar': 
                return this.OnClearMap()
            default:
                return null
        }
    }

    onCloseModal = () => {
        this.setState({
            modal_itinerarios: false,
            modal_calor: false,
            modal_geocercas: false,
            modal_validacion: false,
        });
    }

    onGetItinerarios = () => {
        if (this.props.consulta_vehiculo.length > 0) {
            this.setState({
                modal_itinerarios: false,
                modal_validacion: false,
            });
            this.props.getItinerarios({
                "Device": this.props.consulta_vehiculo[0], 
                "Inicio": this.props.consulta_fechainit,
                "Fin": this.props.consulta_fechaend
            });
        } else {
            this.setState({
                modal_validacion: true,
            });
        }
    }

    onGetMapaCalor = () => {
        console.log('Mapa Calor');
        if (this.props.consulta_vehiculo.length > 0) {
            this.setState({
                modal_calor: false,
                modal_validacion: false,
            });
             this.props.getMapaCalor({
                 "Device": this.props.consulta_vehiculo[0], 
                 "Inicio": this.props.consulta_fechainit,
                 "Fin": this.props.consulta_fechaend
             });
        } else {
            this.setState({
                modal_validacion: true,
            });
        }
    }

    OnClearMap = () => {
        console.log('Limpiar');
        this.props.setClearMap();
    }

    handleMarkerClick = () => {
        console.log('Marker Click');
        this.setState({
            car_detalle: true,
        });
    }

    handleCloseCardDetalle = () => {
        this.setState({
            car_detalle: false,
        });
    }

    handleUltimoViaje = () => {
        console.log('Ultimo Viaje');
    }

    handlePoliza = () => {
        console.log('Poliza');
        this.props.getPoliza();
    }

    handleChange_Vehiculo = event => {
        console.log(event.value);
        this.props.setDatosVehiculo([event.value,this.props.posiciones]); 
    }

    render() {

        const combo_vehiculos = [];
        this.props.posiciones.map( posicion => (
            combo_vehiculos.push({ value: posicion.serial, label: posicion.ubiSerial })
        ));

        return (
            <div className="app-dashboard">
                <HeaderContainer />
                <div className="body-dashboard">
                    <div className="barra-dashboard">
                        <SpiderButtonLeft icono="home" mensaje="Home" onSpiderButtonLeftClick={() => this.onHomeClick() }/>
                        <SpiderButtonLeft icono="mobile-alt" mensaje="TAG"/>
                        <SpiderButtonLeft icono="chart-bar" mensaje="Estadísticas" onSpiderButtonLeftClick={() => this.onEstadisticasClick() }/>
                        <SpiderButtonLeft icono="file-invoice" mensaje="Reportes" onSpiderButtonLeftClick={() => this.onReportesClick() }/>
                        <SpiderButtonLeft icono="cogs" mensaje="Configuración" onSpiderButtonLeftClick={() => this.onConfiguracionClick() }/>
                    </div>
                    <div className="main-dashboard">
                        <div className="main-mapa">
                            <div className="toolbox-dashboard">
                                <SpiderButtonToolbox icono="route" mensaje="Itinerarios" color="" onSpiderButtonToolboxClick={() => this.onSelectedSpiderButtonToolbox("Itinerarios") } />
                                <SpiderButtonToolbox icono="fire-alt" mensaje="Calor" color="" onSpiderButtonToolboxClick={() => this.onSelectedSpiderButtonToolbox("Calor") }/>
                                <SpiderButtonToolbox icono="dot-circle" mensaje="Geocercas" color="" onSpiderButtonToolboxClick={() => this.onSelectedSpiderButtonToolbox("Geocercas") }/>
                                <SpiderButtonToolbox icono="eraser" mensaje="Limpiar" color="" onSpiderButtonToolboxClick={() => this.onSelectedSpiderButtonToolbox("Limpiar") }/>
                            </div>
                            <div className="toolbox-search">
                                <NoSsr>
                                    <Grid container spacing={8}>
                                        <Grid item xs={6}>
                                            <Select
                                                options={suggestions}
                                                placeholder="Grupo.." 
                                                />
                                        </Grid>
                                        <Grid item xs={6}>
                                            <Select
                                                options={combo_vehiculos}
                                                placeholder="Vehiculo..."
                                                onChange={this.handleChange_Vehiculo}
                                                />
                                        </Grid>
                                    </Grid>
                                </NoSsr>
                            </div>
                            <div className="indicadores-dashboard">
                                <IndicadoresContainer />
                            </div>
                            {this.props.itinerarios.length ?
                                <div className="itinerarios-dashboard">
                                    <ItinerariosContainer
                                        n_itinerarios = {this.props.itinerarios[0]}
                                        itinerarios = {this.props.itinerarios[1]}
                                    />
                                </div> :
                                <div></div>
                            }
                            {this.state.car_detalle ? 
                                <div className="car-detalle-dashboard">
                                    <SpiderCarDetalle 
                                        onCloseCardDetalle = {this.handleCloseCardDetalle}
                                        onVerUltimoViaje = {this.handleUltimoViaje}
                                        onVerPoliza = {this.handlePoliza}
                                    />
                                </div>
                                :
                                null
                            }
                            {this.props.posiciones.length ?
                                <SpiderMapContainer 
                                    onMarkerClick = {this.handleMarkerClick}
                                />
                                : renderMapa()
                            }
                        </div>
                    </div>
                    <ModalContainer 
                        posiciones= {this.props.posiciones}
                        boolItinerarios={this.state.modal_itinerarios}  
                        boolCalor={this.state.modal_calor}
                        boolGeocercas={this.state.modal_geocercas}
                        boolValidacion={this.state.modal_validacion}
                        onCloseModal={() => this.onCloseModal() }
                        onGetItinerarios={() => this.onGetItinerarios() }
                        onGetMapaCalor={() => this.onGetMapaCalor()}
                        />
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    setPosiciones: value => dispatch(fetchPosiciones(value)),
    getItinerarios: value => dispatch(fetchItinerarios(value)),
    getMapaCalor: value => dispatch(fetchCalor(value)),
    setClearMap: () => dispatch(fetchClearMap()),
    setDatosVehiculo: value => dispatch(fetchDatosVehiculo(value)),
    setGeocercas: value => dispatch(fetchGeocercas(value)),
});

const mapStatetoProps = state => ({
    posiciones: state.posiciones,
    itinerarios: state.itinerarios,
    consulta_vehiculo: state.consulta_vehiculo,
    consulta_fechainit: state.consulta_fechainit,
    consulta_fechaend: state.consulta_fechaend,
    geocercas: state.geocercas,
});

export default connect(mapStatetoProps, mapDispatchToProps)(DashboardContainer);