export const SET_POSICIONES = 'SET_POSICIONES';
export const GET_ITINERARIOS = 'GET_ITINERARIOS';
export const GET_VIAJE = 'GET_VIAJE';
export const GET_EVENTOS = 'GET_EVENTOS';
export const GET_MAPACALOR = 'GET_MAPACALOR';
export const SET_CONSULTA_VEHICULO = 'SET_CONSULTA_VEHICULO';
export const SET_CONSULTA_FECHAINIT = 'SET_CONSULTA_FECHAINIT';
export const SET_CONSULTA_FECHAEND = 'SET_CONSULTA_FECHAEND';
export const CLEAR_MAP = 'CLEAR_MAP';
export const SET_CURRENT_FIT = 'SET_CURRENT_FIT';
export const SET_DATOS_VEHICULO = 'SET_DATOS_VEHICULO';
export const SET_GEOCERCAS = 'SET_GEOCERCAS';

const setPosiciones = payload => ({ type: SET_POSICIONES, payload });
const getItinerarios = payload => ({ type: GET_ITINERARIOS, payload });
const getViaje = payload => ({ type: GET_VIAJE, payload });
const getCalor = payload => ({ type: GET_MAPACALOR, payload });
const setConsultaVehiculo = payload => ({type: SET_CONSULTA_VEHICULO, payload});
const setConsultaFechaInit = payload => ({type: SET_CONSULTA_FECHAINIT, payload});
const setConsultaFechaEnd = payload => ({type: SET_CONSULTA_FECHAEND, payload});
const setClearMap = () => ({type: CLEAR_MAP});
const setCurrentFit = payload => ({ type: SET_CURRENT_FIT, payload });
const setDatosVehiculo = payload => ({ type: SET_DATOS_VEHICULO, payload});
const setGeocercas = payload => ({ type: SET_GEOCERCAS, payload });

const url_posiciones = "https://ubimx.com/api/spider/location";
const url_itinerarios = "https://ubimx.com/api/fleet/trip/trips";
const url_viaje = "https://ubimx.com/api/fleet/trip/route";
const url_calor = "https://ubimx.com/api/spider/location/heatmap";
const api_data_posiciones = {"Path": ",5c61e8d760bc2703a75e373c,friendly"};

export const fetchPosiciones = payload => {
    return dispatch => {
        return fetch(url_posiciones, {
            method: 'POST',
            body: JSON.stringify(api_data_posiciones),
            headers: { 
                'Content-Type': 'application/json'
            }
        }).then(
            data => (data.json())
        ).then(
            posiciones_data => {
                let array = posiciones_data.resultado;
                // Modificar estado de posiciones
                dispatch(setPosiciones(array));
            }
        );
    }
}

export const fetchItinerarios = payload => {

    return dispatch => {
        return fetch(url_itinerarios, {
            method: 'POST',
            body: JSON.stringify(payload),
            headers: { 
                'Content-Type': 'application/json'
            }
        }).then(
            data => (data.json())
        ).then(
            itinerarios_data => {
                let array = itinerarios_data.resultado;
                if (array === 'none') {
                    dispatch(getItinerarios([0, []]));
                }else {
                    dispatch(getItinerarios([array.length, array]));
                }
            }   
        )
    }
}

export const fetchViaje = payload => {
    console.log(payload);
    return dispatch => {
        return fetch(url_viaje, {
            method: 'POST',
            body: JSON.stringify(payload),
            headers: { 
                'Content-Type': 'application/json'
            }
        }).then(
            data => (data.json())
        ).then(
            viaje_data => {
                let array = viaje_data.resultado;

                const pathviaje = [];
                array.snappeds[0].snappedPoints.map( puntos => {
                    return(
                        pathviaje.push({lat: puntos.location.latitude, lng: puntos.location.longitude})
                    )
                })

                const eventosviaje = [];
                array.events.map( (evento, index) => {
                    return(
                        eventosviaje.push({id: index ,event: evento.event, latitude: evento.latitude, longitude: evento.longitude, date: evento.date})
                    )
                })

                dispatch(setCurrentFit('VIAJE'));
                dispatch(getViaje({pathviaje,eventosviaje}));
            }
        )
    }
}

export const fetchCalor = payload => {
    return dispatch => {
        return fetch(url_calor, {
            method: 'POST',
            body: JSON.stringify(payload),
            headers: { 
                'Content-Type': 'application/json'
            }
        }).then(
            data => (data.json())
        ).then(
            calor_data => {
                let array = calor_data.resultado;
                //console.log(array);
                const datacalor = [];
                array.map( (puntos, index) => {
                    return(
                        //datacalor.push({lat: puntos.location.coordinates[0], lng: puntos.location.coordinates[1]})
                        datacalor.push(new window.google.maps.LatLng(puntos.location.coordinates[0], puntos.location.coordinates[1]))
                    )
                })
                dispatch(setCurrentFit('CALOR'));
                dispatch(getCalor(datacalor));
            }
        )
    }
}

export const fetchConsultaVehiculo = payload => {
    return setConsultaVehiculo(payload);
}

export const fetchConsultaFechaInit = payload => {
    return setConsultaFechaInit(payload);
}

export const fetchConsultaFechaEnd = payload => {
    return setConsultaFechaEnd(payload);
}

export const fetchClearMap = () => {
    return setClearMap();
}

export const fetchDatosVehiculo = payload => {
    
    let cVehiculo = payload[1].find(
            function(vehiculo) { return vehiculo.serial === payload[0] }
        )

    return setDatosVehiculo({ 
        activacion: true, 
        center: { lat: cVehiculo.location.location.coordinates[0], lng: cVehiculo.location.location.coordinates[1] }, 
        zoom: 18 });
}

export const fetchGeocercas = payload => {
    return setGeocercas(payload);
}