import { createStore, applyMiddleware, compose} from 'redux';
import thunk from 'redux-thunk';
import { estados } from './../reducers'; 

const initialState = {
    posiciones: [],
    itinerarios: [],
    viaje: {pathviaje: [], eventosviaje: []},
    calor: [],
    consulta_vehiculo: [],
    consulta_fechainit: '',
    consulta_fechaend: '',
    datos_vehiculos: { activacion: false, center: { lat: 25.6032912, lng: -99.6076541 }, zoom: 8 },
    current_fit: 'NA',
    geocercas: false,
  };

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

export const store = createStore(
    estados,
    initialState,
    composeEnhancers(applyMiddleware(thunk))
);